import '../../node_modules/bootstrap/dist/css/bootstrap.min.css'
import styled from 'styled-components'
import { useState, useEffect, useRef } from 'react';
import $ from 'jquery';

const StyledButton = styled.button`
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    font-size: 12px;
    color: orange;
    font-family: var(--bs-body-font-family);

    &:hover {
        background-color : white;
        color: black;
        font-weight: bold;
    `

const StyledLi = styled.li`
    background-color: ${props => (props.priorite === 'faible' ? 'papayawhip' : props.priorite === 'normale' ? 'orange' : 'red')};
    color: black;
    `

const StyledButton3 = styled.button`
    background-color: cornsilk;
    font-weight: bold;

    &:hover {
        background-color: pink
    }
    `

const StyledSelect = styled.select`
    background-color: #00000000;
    border: 0;
    `

export default function Tache({ tacheInfo, onClick, onClick2, handleRename, handlePriorityChange }) {
    const [nouveauNom, setNouveauNom] = useState('');
    const [estVisible, setEstVisible] = useState(false);
    const slideRef = useRef(null); 
    const premierRendu = useRef(true);
    useEffect(() => {
        if (!premierRendu.current) {
          $(slideRef.current).slideToggle('fast');
        } else {
          premierRendu.current = false;
        }
      }, [estVisible]);

    const handleChangeNom = (e) => {
        if (e.target.value !== null) {
            setNouveauNom(e.target.value);
        }
    };

    const handleRenameClick = () => {
        handleRename(tacheInfo.id, nouveauNom);
        setNouveauNom('');
        if (nouveauNom !== '') {
            setEstVisible(false)
        }
    }

    const handleKeyDown = (event) => {
        if (event.key === 'Enter') {
            handleRename(tacheInfo.id, nouveauNom);
            setNouveauNom('');
            if (nouveauNom !== '') {
                setEstVisible(false)
            }
        }
    }

    //affichage
    return (
        <StyledLi priorite={tacheInfo.priorit} className='list-group-item d-flex flex-wrap gap-1 justify-content-evenly align-items-center flex-column'>{tacheInfo.nom}
            <div className='d-flex flex-row-reverse gap-1'>
                <StyledButton onClick={onClick} title='Supprimer la tche'>&#10060;</StyledButton>
                <StyledButton onClick={onClick2} title='Dupliquer la tche'>&#x29C9;</StyledButton>
                <StyledButton onClick={() => setEstVisible(!estVisible)} title='Renommer la tche'>
                    {!estVisible ?
                        (<span dangerouslySetInnerHTML={{ __html: '&#x270F;' }}></span>) :
                        (<span dangerouslySetInnerHTML={{ __html: '&#9650;' }}></span>)}
                </StyledButton>
                <div>
                    <label htmlFor={tacheInfo.id + "priorite"}>Priorit :&nbsp;</label>
                    <StyledSelect
                        id={tacheInfo.id + "priorite"}
                        className='text-center'
                        value={tacheInfo.priorit}
                        onChange={event => handlePriorityChange(tacheInfo.id, event)}>
                        <option value="faible">Faible</option>
                        <option value="normale">Normale</option>
                        <option value="importante">Importante</option>
                    </StyledSelect>
                </div>
            </div>
            <div ref={slideRef} style={{ display: 'none' }}>
                <div className='flex-nowrap p-2'>
                    <input
                        id={tacheInfo.id + 1}
                        autoFocus
                        className='text-center'
                        type="text"
                        // value={nouveauNom}
                        placeholder='Renommer la tche'
                        onChange={handleChangeNom}
                        onKeyDown={handleKeyDown} />
                    <StyledButton3 onClick={handleRenameClick} title='Valider'>&#10003;</StyledButton3>
                </div>
            </div>


        </StyledLi>
    )
}