import '../../node_modules/bootstrap/dist/css/bootstrap.min.css'
import styled from 'styled-components'
import { useState } from 'react'

const StyledButton2 = styled.button`
    background-color: black;
    color: orange;

&:hover {
    color: black;
    background-color: orange;
}
`
export default function TacheForm({ handleAdd }) {
    //state
    const [nouvelleTache, setNouvelleTache] = useState("")

    //comportement
    const handleSubmit = (event) => {
        event.preventDefault();
        const id = new Date().getTime()
        const nom = nouvelleTache
        const tacheAAjouter = ({ id: id, nom: nom, priorit: "normale" })
        if (nom !== '') {
            handleAdd(tacheAAjouter)
            setNouvelleTache("")
        }
    }

    const handleChange = (event) => {
        setNouvelleTache(event.target.value)
    }

    //affichage
    return (
        <form action="submit" onSubmit={handleSubmit} className='text-center my-3'>
            <input
                id='ajouterTache'
                className='text-center'
                value={nouvelleTache}
                type='text'
                placeholder='Ajouter une tche'
                onChange={handleChange} />
            <StyledButton2 title='Ajouter une tche'>Ajouter</StyledButton2>
        </form>
    )
}

//Gestion du formulaire
//1.Creation du formulaire
//2.soumission du formulaire (onSubmit / handleSubmit)
//3.collecte des donnes du formulaire (onChange / handleChange)